import React, {useState} from 'react'
import ProductTile from './ProductTile'
import Title from './Title'
import arrowRight from './Icons/arrowRight.svg'
import arrowLeft from './Icons/arrowLeft.svg'

class Slider extends React.Component {
    constructor() {
        super()

        this.state = {
            translate: 0,
            deskWidth: 8,
            mobWidth: 21,
            innerWidth: window.innerWidth,
            touchStart: 0,
            touchEnd: 0
        }
        this.swipeLeft = this
            .swipeLeft
            .bind(this)

        this.swipeRight = this
            .swipeRight
            .bind(this)

        this.getInnerWidth = this
            .getInnerWidth
            .bind(this)

        this.handleTouchStart = this
            .handleTouchStart
            .bind(this)

        this.handleTouchMove = this
            .handleTouchMove
            .bind(this)

        this.handleTouchEnd = this
            .handleTouchEnd
            .bind(this)

    }

    handleTouchStart(e) {
        this.setState({touchStart: e.targetTouches[0].clientX})
    }

    handleTouchMove(e) {
        this.setState({touchEnd: e.targetTouches[0].clientX})
    }

    handleTouchEnd() {
        const {touchStart, touchEnd} = this.state
        if (touchStart - touchEnd > 10) {
            this.swipeRight()
        }

        if (touchStart - touchEnd < -10) {
            this.swipeLeft()
        }
    }

    getInnerWidth() {
        this.setState({innerWidth: window.innerWidth})
    }

    componentDidMount() {
        window.addEventListener('resize', this.getInnerWidth)
    }

    swipeLeft() {
        const {translate, deskWidth, mobWidth, innerWidth} = this.state;
        const columnWidth = 100 / 25;
        const deskTranslation = deskWidth * columnWidth + columnWidth;
        const mobTranslation = mobWidth * columnWidth + columnWidth;

        if (translate < 0) {
            if (innerWidth > 767) {
                this.setState({
                    translate: translate + deskTranslation
                })
            } else {
                this.setState({
                    translate: translate + mobTranslation
                })
            }
        }
    }

    swipeRight() {
        const {translate, deskWidth, mobWidth, innerWidth} = this.state;
        const columnWidth = 100 / 25;
        const deskTranslation = deskWidth * columnWidth + columnWidth;
        const mobTranslation = mobWidth * columnWidth + columnWidth;
        const deskLimit = deskTranslation * 4;
        const mobLimit = mobTranslation * 5;

        if (innerWidth > 767) {
            if (translate > -deskLimit) {
                this.setState({
                    translate: translate - deskTranslation
                })
            }
        } else {
            if (translate > -mobLimit) {
                this.setState({
                    translate: translate - mobTranslation
                })
            }
        }

    }

    render() {

        const {translate, deskWidth, mobWidth} = this.state

        console.log(translate)
        return (
            <div className="col-25 flex-col slider">
                <Title
                    fontSize="small"
                    width="full"
                    marginBottom="none"
                    title="Lacoste recomends"/>
                <div className='switch mgt-2 mgt-m-4'>
                    <button onClick={this.swipeLeft}><img src={arrowLeft} className="icon" alt="search"/></button>
                    <button onClick={this.swipeRight}><img src={arrowRight} className="icon" alt="search"/></button>
                </div>
                <div className="col-25 overlay">
                    <div
                        onTouchStart={this.handleTouchStart}
                        onTouchMove={this.handleTouchMove}
                        onTouchEnd={this.handleTouchEnd}
                        className="rownowrap"
                        style={{
                        transform: `translateX(${translate}vw)`,
                        transition: 'all 400ms 200ms cubic-bezier(0.645, 0.045, 0.355, 1)'
                    }}>
                        {this
                            .props
                            .images
                            .map((img, index) => <ProductTile
                                key={index}
                                deskWidth={deskWidth}
                                mobWidth={mobWidth}
                                img={img}
                                alt="image"/>)}
                    </div>
                </div>
            </div>
        )
    }
}

export default Slider