import React from 'react';
import Navigation from './Navigation';

class Header2 extends React.Component {
    constructor() {
        super()

        this.elt1 = React.createRef();
        this.elt2 = React.createRef();
        this.elt3 = React.createRef();

        this.parallax = this
            .parallax
            .bind(this)
        this.state = {
            link: 'For him',
            text1pos: 0,
            text2pos: 300,
            videopos: 0,
            scaling: 'scale-10',
            visible: 'appear'
        }
    }

    parallax() {
        const elt1Position = this
            .elt1
            .current
            .getBoundingClientRect()
            .top;

        const elt2Position = this
            .elt2
            .current
            .getBoundingClientRect()
            .top;

        const elt3Position = this
            .elt3
            .current
            .getBoundingClientRect()
            .top;
        console.log(elt1Position, window.innerHeight)

        if (elt1Position >= -window.innerHeight) {
            this.setState({
                text1pos: -elt1Position / 5,
                text2pos: elt1Position / 5 + 300,
                scaling: 'scale-10',
                visible: 'appear'
            })

        } else {
            this.setState({text1pos: 0, text2pos: 0, scaling: 'scale-4', visible: ''})
        }

    }

    componentDidMount() {
        window.addEventListener('scroll', this.parallax)
    }

    render() {
        const {
            link,
            text1pos,
            text2pos,
            videopos,
            scaling,
            visible
        } = this.state

        return (
            <div ref={this.elt1} className={`col-25 heading ${this.props.bgcolor} fixed`}>
                <div
                    style={{
                    transform: `translateY(${videopos}px)`
                }}
                    className="offset-7 col-11 offset-s-2 col-s-21 video-inner"
                    dangerouslySetInnerHTML={{
                    __html: `<video class="video" autoplay loop muted playsinline> <source src=${this.props.video} alt="video" type="video/mp4"/></video>`
                }}/>
                <div
                    className={`title-double ${scaling} mgt-4 offset-1 col-23 justify-center absolute`}>
                    <p
                        ref={this.elt2}
                        className="very-big-title light"
                        style={{
                        transform: `translateY(${text1pos}px)`
                    }}>Gift
                    </p>
                    <p className="space"></p>
                    <p
                        ref={this.elt3}
                        className="very-big-title light"
                        style={{
                        transform: `translateY(${text2pos}px)`
                    }}>Guide</p>
                </div>
                <p className={`offset-7 col-11 text-m text-top mgt-7 light ${visible}`}>Lorem
                    ipsum Nam Donec malesuada leo, vehicula commodo Morbi tincidunt lacus aliquet
                    ultrices tempor scelerisque neque Cras sed vitae eget Morbi rhoncus feugiat
                    ultricies.</p>
            </div>
        )
    }
}

export default Header2