import React from 'react'
import Nav from './Nav';
import Header from './Header';
import Header2 from './Header2';
import Tile from './Tile'
import Slider from './Slider'
import Title from './Title'
import video from './videos/video.mp4';
import video2 from './videos/video2.mp4';
import videokids from './videos/videokids.mp4';
import videomen from './videos/videomen.mp4';
import videowomen from './videos/videowomen.mp4';
import videowomen2 from './videos/videowomen2.mp4';
import men from './images/men.jpg'
import women from './images/women.jpg'
import kids from './images/kids.jpg'
import image2 from './images/image2.jpg'
import bag1 from './images/bag1.jpg'
import bag2 from './images/bag2.jpg'
import giftmen1 from './images/giftmen1.jpg'
import giftmen2 from './images/giftmen2.jpg'
import underwearMen from './images/underwearMen.jpg'
import leathergoodsMen from './images/leathergoodsMen.jpg'
import fragrancesMen from './images/fragrancesMen.jpg'
import imgProductMen1 from './images/imgProductMen1.jpg'
import imgProductMen2 from './images/imgProductMen2.jpg'
import imgProductMen3 from './images/imgProductMen3.jpg'
import imgProductMen4 from './images/imgProductMen4.jpg'
import imgProductMen5 from './images/imgProductMen5.jpg'
import imgProductMen6 from './images/imgProductMen6.jpg'
import imgProductWomen1 from './images/imgProductWomen1.jpg'
import imgProductWomen2 from './images/imgProductWomen2.jpg'
import imgProductWomen3 from './images/imgProductWomen3.jpg'
import imgProductWomen4 from './images/imgProductWomen4.jpg'
import imgProductWomen5 from './images/imgProductWomen5.jpg'
import imgProductWomen6 from './images/imgProductWomen6.jpg'
import imgProductKids1 from './images/imgProductKids1.jpg'
import imgProductKids2 from './images/imgProductKids2.jpg'
import imgProductKids3 from './images/imgProductKids3.jpg'
import imgProductKids4 from './images/imgProductKids4.jpg'
import imgProductKids5 from './images/imgProductKids5.jpg'
import imgProductKids6 from './images/imgProductKids6.jpg'
import accessoriesMen from './images/accessoriesMen.jpg'
import Sticky from './Sticky'
import Parallax from './Parallax'
import Supercomponent from './Supercomponent'
import Intro from './Intro'

class Container extends React.Component {
    constructor() {
        super()

        this.node = React.createRef()
        this.node2 = React.createRef()

        this.state = {
            img1: bag1,
            img2: bag2,
            videos: video,
            title: "Christmas gift guide",
            hide1: '',
            hide2: '',
            hide3: '',
            bgcolor: 'red',
            imgCat: {
                cat1: {
                    title: 'Accessories',
                    img: accessoriesMen
                },
                cat2: {
                    title: 'Fragrances',
                    img: fragrancesMen
                },
                cat3: {
                    title: 'Underwear',
                    img: underwearMen
                }
            },
            imgPdt: [
                imgProductMen1,
                imgProductMen2,
                imgProductMen3,
                imgProductMen4,
                imgProductMen5,
                imgProductMen6
            ]
        }
        this.handleClick = this
            .handleClick
            .bind(this)

        this.changeCatMen = this
            .changeCatMen
            .bind(this)

        this.changeCatWomen = this
            .changeCatWomen
            .bind(this)

        this.changeCatKids = this
            .changeCatKids
            .bind(this)
    }

    handleClick() {
        this.setState({img1: giftmen1, img2: giftmen2})
    }

    changeCatMen(e) {
        e.preventDefault();
        const elt = this.node.current;
        const elt2 = this.node2.current;

        elt2
            .classList
            .add('trans');
        elt
            .classList
            .add(this.state.bgcolor);

        setTimeout(() => this.setState({
            videos: videomen,
            title: "Men's gift guide",
            hide1: 'none',
            hide2: '',
            hide3: '',
            bgcolor: 'green',
            imgCat: {
                cat1: {
                    title: 'Accessories',
                    img: accessoriesMen
                },
                cat2: {
                    title: 'Fragrances',
                    img: fragrancesMen
                },
                cat3: {
                    title: 'Underwear',
                    img: underwearMen
                }
            },
            imgPdt: [
                imgProductMen1,
                imgProductMen2,
                imgProductMen3,
                imgProductMen4,
                imgProductMen5,
                imgProductMen6
            ]
        }), 500);

        setTimeout(() => elt2.classList.remove('trans'), 0);
        setTimeout(() => elt.classList.remove('green', 'red', 'pink', 'blue'), 0);
    }

    changeCatWomen(e) {
        e.preventDefault();
        const elt = this.node.current;
        const elt2 = this.node2.current;

        elt2
            .classList
            .add('trans');
        elt
            .classList
            .add(this.state.bgcolor);

        setTimeout(() => this.setState({
            videos: videowomen2,
            title: "Women's gift guide",
            hide1: '',
            hide2: 'none',
            hide3: '',
            bgcolor: 'pink',
            imgCat: {
                cat2: {
                    title: 'Accessories',
                    img: accessoriesMen
                },
                cat3: {
                    title: 'Fragrances',
                    img: fragrancesMen
                },
                cat1: {
                    title: 'Underwear',
                    img: underwearMen
                }
            },
            imgPdt: [
                imgProductWomen1,
                imgProductWomen2,
                imgProductWomen3,
                imgProductWomen4,
                imgProductWomen5,
                imgProductWomen6
            ]
        }), 500);

        setTimeout(() => elt2.classList.remove('trans'), 0);
        setTimeout(() => elt.classList.remove('green', 'red', 'pink', 'blue'), 0);
    }

    changeCatKids(e) {
        e.preventDefault();
        const elt = this.node.current;
        const elt2 = this.node2.current;

        elt2
            .classList
            .add('trans');
        elt
            .classList
            .add(this.state.bgcolor);

        setTimeout(() => this.setState({
            videos: videokids,
            title: "Kids gift guide",
            hide1: '',
            hide2: '',
            hide3: 'none',
            bgcolor: 'blue',
            imgCat: {
                cat1: {
                    title: 'Accessories',
                    img: accessoriesMen
                },
                cat2: {
                    title: 'Fragrances',
                    img: fragrancesMen
                },
                cat3: {
                    title: 'Underwear',
                    img: underwearMen
                }
            },
            imgPdt: [
                imgProductKids1,
                imgProductKids2,
                imgProductKids3,
                imgProductKids4,
                imgProductKids5,
                imgProductKids6
            ]
        }), 500);

        setTimeout(() => elt2.classList.remove('trans'), 0);
        setTimeout(() => elt.classList.remove('green', 'red', 'pink', 'blue'), 0);
    }

    render() {
        const {
            img1,
            img2,
            videos,
            title,
            hide1,
            hide2,
            hide3,
            bgcolor,
            imgCat,
            imgPdt
        } = this.state

        return (

            <div ref={this.node} className="row load">
                <div ref={this.node2} className="row">
                    <Nav/>
                    <Header
                        eventmen={this.changeCatMen}
                        eventwomen={this.changeCatWomen}
                        eventkids={this.changeCatKids}
                        video={videos}
                        url={video}
                        title={title}
                        hide1={hide1}
                        hide2={hide2}
                        hide3={hide3}
                        bgcolor={bgcolor}/>

                    <Title
                        fontSize="medium"
                        button={< a href = "" className = "btn btn-dark" > All products </a>}
                        marginBottom="medium"
                        width="mid"
                        title="Gifts for him"
                        text="Lorem ipsum Nam Donec malesuada leo,
        vehicula commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque
        Cras sed vitae eget Morbi rhoncus feugiat ultricies."/>

                    <Parallax event={this.handleClick} img1={img1} img2={img2}/>

                    <Title
                        fontSize="medium"
                        button={<a href ="" className="btn btn-dark">All products</a>}
                        marginBottom="medium"
                        width="mid"
                        title="Find by category"
                        text="Lorem ipsum Nam Donec malesuada leo,
        vehicula commodo Morbi tincidunt lacus aliquet ultrices tempor scelerisque neque
        Cras sed vitae eget Morbi rhoncus feugiat ultricies."/>

                    <Tile
                        numberOf='two'
                        alignment='start'
                        title={imgCat.cat1.title}
                        cta="See products"
                        img={imgCat.cat1.img}
                        alt="image"/>

                    <Tile
                        numberOf='two'
                        alignment='start'
                        title={imgCat.cat2.title}
                        cta="See products"
                        img={imgCat.cat2.img}
                        alt="image"/>

                    <Tile
                        numberOf='two'
                        alignment='center'
                        title={imgCat.cat3.title}
                        cta="See products"
                        img={imgCat.cat3.img}
                        alt="image"/>

                    <Slider images={imgPdt}/>

                    <Title
                        fontSize="small"
                        width="full"
                        marginBottom="none"
                        title="You may also like"/>
                    <Tile
                        numberOf='two'
                        title="For her"
                        cta="All products"
                        img={women}
                        alt="image"/>

                    <Tile
                        numberOf='two'
                        title="For kids"
                        cta="All products"
                        img={kids}
                        alt="image"/>

                </div>
            </div>
        )
    }
}

export default Container